const blockModal = document.querySelector('.modal__nav')

if (blockModal) {
    // class blocks
    const body = document.querySelector('body')
    const modal = document.querySelector('.modal__nav')
    const btnClose = document.querySelector('.modal-close-btn__nav')
    const btnOpenModal = document.querySelector('.stream__btn')
    
    // add class
    const overflow = 'overflow'
    
    const dataModalActive = 'data-modal-active'
    const dataTrue = 'true'
    const dataFalse = 'false'
    
    // modal.setAttribute(dataModalActive, dataTrue)
    
    btnOpenModal.addEventListener('click', function () {
        modal.setAttribute(dataModalActive, dataTrue)
        body.classList.add(overflow)
    })

    btnClose.addEventListener('click', function () {
        modal.setAttribute(dataModalActive, dataFalse)
        body.classList.remove(overflow)
    })
}
