const blockModal = document.querySelector('.modal__add-nickname')

if (blockModal) {
    const modals = document.querySelectorAll('.modal')

    // class blocks
    const body = document.querySelector('body')
    const modal = document.querySelector('.modal__add-nickname')
    const btnClose = document.querySelector('.modal-close-btn__add-nickname')
    const btnOpenModal = document.querySelector('.header__btn')

    // add class
    const overflow = 'overflow'

    const dataModalActive = 'data-modal-active'
    const dataTrue = 'true'
    const dataFalse = 'false'

    btnOpenModal.addEventListener('click', function () {
        modal.setAttribute(dataModalActive, dataTrue)
        body.classList.add(overflow)
    })

    btnClose.addEventListener('click', function () {
        modal.setAttribute(dataModalActive, dataFalse)
        body.classList.remove(overflow)
    })
    
    modals.forEach(function (modal) {
        modal.onclick = function (event) {
            if (event.target == modal) {
                modal.setAttribute(dataModalActive, dataFalse)
                body.classList.remove(overflow)
            }
        }
    })
}
