const blockContent = document.querySelector('.navigation__content')

if (blockContent) {
    // Получаем все чекбоксы с классом .switch-btn
    const checkboxes = document.querySelectorAll(
        '.switch-btn-full-screen, .switch-btn-cinema-mode, .switch-btn-simplified-chat'
    )

    // Перебираем каждый чекбокс
    checkboxes.forEach((checkbox) => {
        // Добавляем обработчик события change
        checkbox.addEventListener('change', function () {
            // Сохраняем состояние чекбокса в localStorage
            localStorage.setItem('checkbox_' + this.id, this.checked)
        })

        // Проверяем значение в localStorage и устанавливаем его
        const checked =
            localStorage.getItem('checkbox_' + checkbox.id) === 'true'
        checkbox.checked = checked
    })

    // Получаем все радио-кнопки с классом .radio-btn и атрибутом
    const radioButtons = document.querySelectorAll(
        '.radio-btn-arrangement-element, .radio-btn-width-container'
    )

    // Получаем все радио-кнопки с классом .radio-btn и атрибутом
    const radioButtonsArrangementElement = document.querySelectorAll(
        '.radio-btn-arrangement-element'
    )
    // Получаем все радио-кнопки с классом .radio-btn и атрибутом
    const radioButtonsWidthContainer = document.querySelectorAll(
        '.radio-btn-width-container'
    )

    // Проверяем, есть ли радио-кнопки в группе
    if (radioButtonsArrangementElement.length > 0) {
        // Устанавливаем атрибут checked на первую радио-кнопку
        radioButtonsArrangementElement[0].checked = true
    }

    // Проверяем, есть ли радио-кнопки в группе
    if (radioButtonsWidthContainer.length > 0) {
        // Устанавливаем атрибут checked на первую радио-кнопку
        radioButtonsWidthContainer[0].checked = true
    }

    // Перебираем каждую радио-кнопку
    radioButtons.forEach((radioButton) => {
        // Добавляем обработчик события change
        radioButton.addEventListener('change', function () {
            // Сохраняем состояние выбранной радио-кнопки в localStorage
            localStorage.setItem('radio_' + this.name, this.value)
        })

        // Проверяем значение в localStorage и устанавливаем его
        const checkedValue = localStorage.getItem('radio_' + radioButton.name)
        if (checkedValue === radioButton.value) {
            radioButton.checked = true
        }
    })
}
