const blockForm = document.querySelector('.add-nickname')

if (blockForm) {
    const modal = document.querySelector('.modal')

    const regex = /^[a-zA-Z0-9_]+$/
    const inputField = document.getElementById('input-nick')

    const smileTrue = document.getElementById('input-icon-true')
    const smileFalse = document.getElementById('input-icon-false')
    const smileStatic = document.getElementById('input-icon-static')

    const btnSumbitNick = document.getElementById('btn-submit-nick')

    const classActive = 'active'

    const dataModalActive = 'data-modal-active'
    const dataFalse = 'false'

    // Получаем параметры из URL
    const urlParams = new URLSearchParams(window.location.search)
    const inputDataFromUrl = urlParams.get('data')
    
    // Функция для валидации input
    function validateInput(input) {
        if (input === '') {
            // Если поле пустое, удалить оба класса
            inputField.classList.remove('input-true', 'input-false')
            smileTrue.classList.remove(classActive)
            smileFalse.classList.remove(classActive)
            smileStatic.classList.add(classActive)

            btnSumbitNick.setAttribute('data-disabled', 'true')
        } else if (regex.test(input)) {
            // Если ввод соответствует правилам
            inputField.classList.add('input-true')
            inputField.classList.remove('input-false')

            smileTrue.classList.add(classActive)
            smileFalse.classList.remove(classActive)
            smileStatic.classList.remove(classActive)

            // Активируем кнопку при неправильном вводе
            btnSumbitNick.setAttribute('data-disabled', 'false')
        } else {
            // Если ввод не соответствует правилам
            inputField.classList.add('input-false')
            inputField.classList.remove('input-true')

            smileFalse.classList.add(classActive)
            smileTrue.classList.remove(classActive)
            smileStatic.classList.remove(classActive)

            // Де активируем кнопку при неправильном вводе
            btnSumbitNick.setAttribute('data-disabled', 'true')
        }

        inputField.value = input.toLowerCase()
    }

    // Пример использования функции при изменении значения в поле input
    document
        .getElementById('input-nick')
        .addEventListener('input', function () {
            validateInput(this.value)
        })

    inputField.addEventListener('mouseover', function () {
        smileStatic.classList.add('hover')
    })

    inputField.addEventListener('mouseout', function () {
        smileStatic.classList.remove('hover')
    })

    inputField.addEventListener('focus', function () {
        smileStatic.classList.add('focus')
    })

    inputField.addEventListener('blur', function () {
        smileStatic.classList.remove('focus')
    })

    function addUrl() {
        // Получаем значение введенное в input
        const dataInput = inputField.value

        // Получаем элемент iframe по его id
        const iframe = document.getElementById('stream')

        // Формируем новый URL адрес с данными из input
        const newUrl = `https://player.twitch.tv/?channel=${dataInput}&parent=maxuiux.github.io`

        // Устанавливаем новый URL в src атрибут iframe
        iframe.src = newUrl

        const inputData = inputField.value

        // Добавляем данные в адресную строку
        history.pushState(null, null, '?data=' + encodeURIComponent(inputData))

        // Сохраняем данные в localStorage
        localStorage.setItem('data', inputData)

        // данные в title
        const currentTitle = document.title

        if (currentTitle.includes(' - ')) {
            // Если содержит, заменяем старую информацию на новую
            let newTitle = inputData + ' - ' + 'StreamVave'

            document.title = newTitle
        } else {
            // Если не содержит, просто добавляем новую информацию
            document.title = inputData + ' - ' + 'StreamVave'
        }

        modal.setAttribute(dataModalActive, dataFalse)
    }

    btnSumbitNick.addEventListener('click', () => {
        addUrl()
    })

    window.addEventListener(
        'keydown',
        (event) => {
            if (event.defaultPrevented) {
                return // Не делаем ничего, если действие по умолчанию было отменено
            }

            let handled = false
            if (event.key === 'Enter') {
                // Обрабатываем событие для клавиши Enter
                handled = true
                addUrl() // Вызываем функцию при нажатии на Enter
            }
            if (event.key === 'Escape') {
                handled = true
                modal.classList.remove(classActive)
            }

            if (handled) {
                // Предотвращаем "двойное действие", если событие обработано
                event.preventDefault()
            }
        },
        true
    )

    // Используем данные из адресной строки
    if (inputDataFromUrl) {
        // Получаем элемент iframe по его id
        const iframe = document.getElementById('stream')

        inputField.value = inputDataFromUrl

        // Меняем название в title
        document.title = inputDataFromUrl + ' - ' + document.title

        // Формируем новый URL адрес с данными из input
        const newUrl = `https://player.twitch.tv/?channel=${inputDataFromUrl}&parent=maxuiux.github.io`

        // Устанавливаем новый URL в src атрибут iframe
        iframe.src = newUrl
    }

}
